:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
body {
  line-height: 25px;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #7b838a;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.home_banner_area{
    background-image: url("./Images/education-background.jpg");
    z-index: 1;
    min-height: 700px;
    position: relative;
    background-size: cover;
}
.home_banner_area .banner_inner .banner_content p {
  font-size: 20px;
  margin-top: 250px;
  color: #ec1818;
  font-weight: 500;
}
.home_banner_area .banner_inner .banner_content h2 {
  font-size: 39px;
  font-weight: 500;
}
.primary-btn2 {
  margin-top: 20px;
  display: inline-block;
  background: #002347;
  padding: 0px 48px;
  color: #fdc632;
  font-size: 13px;
  font-weight: 500;
  line-height: 48px;
  border-radius: 5px;
  border: 1px solid transparent;
  outline: none !important;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.feature_area {
  padding-bottom: 70px;
}
.section_gap_top {
  padding-top: 130px;
}
h1, h2, h3, h4, h5, h6 {
  font-family:  sans-serif;
  font-weight: 500;
  color: #002347;
}
.main_title p {
  font-size: 16px;
  margin-bottom: 50px;
  font-style: italic;
}
.icon {
  color: #002347;
}
.single_feature {
  position: relative;
  background: #f9f9ff;
  padding: 25px 20px;
  margin-bottom: 30px;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transition-delay: 0s;
}


.main_title h2 {
  font-size: 36px; 
  line-height: 30px;
  font-weight: bold;
  margin-bottom: 15px;
}
.single_course .course_content {
  padding: 10px 20px;
  background: #f9f9ff;
  position: relative;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.registration_area {
  background: #002347;
}
.register_form {
  background: #fff;
  text-align: center;
  padding: 40px;
  margin: 20px;
}
.register_form input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #eeeeee;
  padding: 12px;
}
.register_form h3 {
  font-size: 24px;
  margin-bottom: 0;
}
.register_form .primary-btn {
  width: 100%;
  border-radius: 0;
  border: none;
  text-transform: uppercase;
  font-weight: 500;
  margin-top: 30px;
}
.primary-btn {
  display: inline-block;
  background: #fdc632;
  padding: 0px 48px;
  color: #002347;
  font-size: 13px;
  font-weight: 500;
  line-height: 50px;
  border-radius: 5px;
  outline: none !important;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.footer-area {
  background: #002347;
}

.section_gap {
  padding: 130px 0;
  margin-top: 50px;
}
a:link {
  text-decoration: none;
  text-decoration-line: none;
  text-decoration-style: initial;
  text-decoration-color: initial;

}
ul li link a{
  text-decoration: none;
}
.footer-area .single-footer-widget ul li a {
   -webkit-transition: all 0.3s ease 0s; 
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s; 
   color: #7b838a;
   text-decoration: none;
}
ul li {
  list-style: none;
}
.footer-area .footer-bottom .footer-social a {
  background: #334f6c;
  width: 20px;
  width: 40px;
  display: inline-table;
  height: 40px;
  text-align: center;
  padding-top: 10px;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
.banner_area {
  position: relative;
  background: url("./Images/aboutus.jpg") no-repeat center center;
  z-index: 1;
  min-height: 392px;
  padding-top: 0 !important;
}
.banner_area .banner_inner {
  position: relative;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100%;
  min-height: 392px;
  z-index: 1;
}
.banner_area .banner_inner .overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 35, 71, 0.8);
}
.banner_area .banner_inner .banner_content {
  position: relative;
  z-index: 2;
  color: #fff;
}
.banner_area .banner_inner .banner_content .page_link {
  display: inline-block;
  padding: 7px 20px;
}
.banner_area .banner_inner .banner_content h2 {
  color: #fff;
  font-size: 45px;
  margin-bottom: 0px;
}
.h_blog_item .col-lg-6 {
    vertical-align: middle;
    align-self: center;
}
.h_blog_item .h_blog_img {
  position: relative;
  z-index: 10;
}
.logos {
  height: auto;
  width: 200px;
}
.contact_form .form-group {
  margin-bottom: 10px;
}
.contact_form .form-group .form-control {
  line-height: 26px;
  border: 1px solid #eeeeee;
  font-family: "Roboto", sans-serif;
  border-radius: 0px;
  padding-left: 20px;
}
.nice-select .option.selected {
  font-weight: normal;
}
.contact_info .info_item {
  position: relative;
  padding-left: 45px;
}
.header_area .navbar {
  background: transparent;
  padding: 0px;
  border: 0px;
  border-radius: 0px;
}
.navbar-light .navbar-brand {
  color: rgba(0, 0, 0, .9);
}
.header_area .navbar .nav .nav-item {
  margin-right: 45px;
}


.header_area .navbar .nav .nav-item .nav-link {
  font: 500 14px/80px "Rubik", sans-serif;
  text-transform: capitalize;
  color: #002347;
  padding: 0px;
  padding-top: 0px;
  padding-right: 0px;
  padding-bottom: 0px;
  padding-left: 0px;
  display: inline-block;
}
.header_area .navbar .nav .nav-item.submenu ul .nav-item .nav-link {
  line-height: 45px;
  color: #002347;
  padding: 0px 30px;
  transition: all 150ms linear;
  transition-property: all;
  transition-duration: 150ms;
  transition-timing-function: linear;
  transition-delay: 0s;
  display: block;
  margin-right: 0px;
}
.header_area .navbar .nav .nav-item .nav-link {
  font: 500 14px/80px "Rubik", sans-serif;
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: 500;
  font-stretch: normal;
  font-size: 14px;
  line-height: 80px;
  font-family: Rubik, sans-serif;
  text-transform: capitalize;
  color: #002347;
  padding: 0px;
  display: inline-block;
}



.header_area .navbar .nav .nav-item.submenu ul {
   
    top: 120%;
    left: 0px;
    min-width: 200px;
   
}


.mainbody{
  box-shadow: #002347 5px;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 200px;
  background-color: whitesmoke;
}

